export const GetAllBomListByProductCodeQuery = `query GetAllBomListByProductCodeQuery($product_code: Int!) {
    core_bom(where: {product_code: {_eq: $product_code}}) {
      id
      name
    }
}
`;

export const allBomAssociatedwithATenant = `query allBomAssociatedwithATenant {
  core_bom {
    id
    name
  }
}`;

export const GetBomdetailsByBomId = `query GetBomdetailsByBomId($bom_id: Int!, $active: Boolean!) {
  bom_items(
    where: {bom_id: {_eq: $bom_id}, bom_version: {active: {_eq: $active}}}
  ) {
    bom_id
    bom_version_id
    material_id
    quantity
    total_price
    unit_size
    associated_product_code_bom
    associated_bom_version {
      version_no
      id
      active
      core_bom {
        id
        name
      }
    }
    core_material {
      custom_material_id
      erp_material_id
      gross_weight
      id
      inventory
      lead_time
      material_description
      material_group
      material_name
      material_type
      parent_id
      plm_material_id
      plm_record_id
      product_code
      quantity
      status
      storage_loc
      unit_cost
      unit_of_material
      unit_sale_price
      weight_unit
      material_product_code {
        id
        product_code
      }
    }
    core_bom {
      state         
      tenant_id
    }
  }
}
  `;

