export const GetMaterialMasterDataQuery = `query GetMaterialMasterDataQuery(
    $jump: Int,
    $perPage: Int,
    $searchKeyword: String
    ) {
    core_material_master_aggregate(where: {_or: [
      {custom_material_id: {_ilike: $searchKeyword}},
      {erp_material_id: {_ilike: $searchKeyword}},
      {material_description: {_ilike: $searchKeyword}},
      {material_name: {_ilike: $searchKeyword}},
      {material_type: {_ilike: $searchKeyword}},
      {plm_material_id: {_ilike: $searchKeyword}},
      {unit_of_material: {_ilike: $searchKeyword}},
      {material_product_code: {
        product_code: {_ilike: $searchKeyword}}
      }
    ]}){
      aggregate {
        count
      }
    }
    core_material_master(
      where: {_or: [
        {custom_material_id: {_ilike: $searchKeyword}},
        {erp_material_id: {_ilike: $searchKeyword}},
        {material_description: {_ilike: $searchKeyword}},
        {material_name: {_ilike: $searchKeyword}},
        {material_type: {_ilike: $searchKeyword}},
        {plm_material_id: {_ilike: $searchKeyword}},
        {unit_of_material: {_ilike: $searchKeyword}},
        {material_product_code: {
          product_code: {_ilike: $searchKeyword}}
        }
      ]}
      limit: $perPage
      offset: $jump
    ) {
      custom_material_id
      erp_material_id
      blob_reference_key
      id
      gross_weight
      inventory
      lead_time
      material_description
      material_group
      material_name
      material_type
      others
      parent_id
      plm_material_id
      plm_record_id
      quantity
      status
      storage_loc
      unit_cost
      unit_of_material
      unit_sale_price
      weight_unit
      product_code
      material_product_code {
        id
        product_code
      }
    }
  }
  `;

export const GetAllProductCodes = "query GetAllProductCodes {\n  product_code {\n    id\n    product_code\n  }\n}\n";

