export const GetAllProjectBomListQuery = `query GetAllProjectBomListQuery($projectId: Int!)  {
  core_bom(where: {project_id: {_eq: $projectId}}) {
    id
    name
    bom_versions(where: {active: {_eq: true}}) {
      id
      version_no
      active
    }
    project_id
  }
}`;

export const getParentTags = `query getParentTags($value: String!) {
  tag(where: {name: {_iregex: $value}, parent_id: {_is_null: true}}) {
    parent_id
    name
    id
  }
}`;
export const getChildrenTagsQuery = `query getChildrenTagsQuery($value:String!,$parent_id:Int){
  tag(where: {name: {_iregex: $value}, parent_id: {_eq: $parent_id}}) {
     parent_id
     name
     id
   }
 }`;
export const GetMaterialMasterForBomQueryWithTagFilters = `query GetMaterialMasterForBomQueryWithTagFilters(
  $jump: Int,
  $perPage: Int,
  $searchKeyword: String,
  $materialIds:[Int!]
  ) {
  core_material_master_aggregate(where:{_and:[
    {_or: [
    {custom_material_id: {_ilike: $searchKeyword}},
    {erp_material_id: {_ilike: $searchKeyword}},
    {material_description: {_ilike: $searchKeyword}},
    {material_name: {_ilike: $searchKeyword}},
    {material_type: {_ilike: $searchKeyword}},
    {plm_material_id: {_ilike: $searchKeyword}},
    {unit_of_material: {_ilike: $searchKeyword}},
    {material_product_code: {
      product_code: {_ilike: $searchKeyword}}
    }]},
    {id: {
      _in: $materialIds
    }}
  ]}){
    aggregate {
      count
    }
  }
  core_material_master(
    where:{_and:[
    {_or: [
    {custom_material_id: {_ilike: $searchKeyword}},
    {erp_material_id: {_ilike: $searchKeyword}},
    {material_description: {_ilike: $searchKeyword}},
    {material_name: {_ilike: $searchKeyword}},
    {material_type: {_ilike: $searchKeyword}},
    {plm_material_id: {_ilike: $searchKeyword}},
    {unit_of_material: {_ilike: $searchKeyword}},
    {material_product_code: {
      product_code: {_ilike: $searchKeyword}}
    }]},
    {id: {
      _in: $materialIds
    }}
  ]}
    limit: $perPage
    offset: $jump
  ) {
    custom_material_id
    erp_material_id
    id
    lead_time
    material_description
    material_name
    material_type
    plm_material_id
    unit_of_material
    product_code
    material_product_code {
      id
      product_code
    }
  }
}
`;
export const getMaterialTags = `query getMaterialTags(
  $tags:[Int!]!
){
  tag_material(where: {tag_id: {_in: $tags}}) {
    material_id
  }
}
`;
export const GetAllFormTypeListQuery = `query GetAllFormTypeListQuery {
  core_form_types {
    enabled
    id
    name
  }
}`;
export const GetDetailFormTemplateQuery = `query GetDetailFormTemplateQuery($id: Int!) {
  core_form_templates_by_pk(id: $id) {
    name
    id
    core_form_type {
      name
    }
    template_versions(where: {active: {_eq: true}}) {
      active
      version_id
      id
      template_fields {
        autogenerated
        caption
        custom_list_id
        field_id
        field_name
        field_type_id
        fixed
        required
        template_version_id
        form_field {
          key
        }
      }
    }
  }
}`;

export const GetAllFormTemplatesQuery = `query GetAllFormTemplatesQuery{
  core_form_templates {
    name
    id
    core_form_type {
      name
    }
    template_versions(where: {active: {_eq: true}}) {
      active
      version_id
    }
  }
}`;
export const GetBomMaterialMappingConfigQuery = `query GetBomMaterialMappingConfigQuery($tenant_id: Int!) {
  tenant_defaults(where: {tenant_id: {_eq: $tenant_id}}) {
    bom
    material
  }
}`;

export const GetBomCurrentStateQuery = `query GetBomCurrentStateQuery($bom_id: Int!) {
  core_bom(where: {id: {_eq: $bom_id}}) {
    state
    name
    checked_out_by
  }
}`;
